<template>
  <div class="main-box">
    <div class="main">
      <div class="middle">
        <div class="left">
          <live :name="gardenRecord.name"></live>
        </div>
        <div class="right">
          <p class="refresh">数据每隔五分钟自动刷新
            <button class="btn1" @click="refresh">
              <i class="iconfont">&#xe667;</i>刷新</button>
          </p>
          <ul class="ranking">
            <li>
              <h3 class="title">
                <em></em>以出勤记录情况排名</h3>
              <div class="content">
                <no-data v-if="attendance.xData[0] === '暂无数据' || attendance.data.findIndex(v => v !== 0) === -1" :item="'出勤'" :bg="true" />
                <bar
                  :settings="attendance"
                   v-else
                ></bar>
              </div>
            </li>
            <li>
              <h3 class="title">
                <em></em>以体温报警人数排名</h3>
              <div class="content">
                <no-data v-if="temperature.xData[0] === '暂无数据' || temperature.data.findIndex(v => v !== 0) === -1" :item="'体温'" :bg="true" />
                <bar
                  :settings="temperature"
                  v-else
                ></bar>
              </div>
            </li>
          </ul>
        </div>
      </div>
      <div class="line"></div>
      <div class="bottom">
        <ul class="statistics">
          <li class="warn-box">
            <div class="title-box">
              <h3 class="title">
                <em></em>
                今日出勤统计
                <i class="percent">请假预警值{{warnPercent.work}}%</i>
              </h3>
              <el-message :value="warnPercent.work" :item="'请假'" @getMessage="getMessage(arguments, 'work')"></el-message>
            </div>
            <div class="content">
              <p class="warn" v-if="warn.work.show">
                <i style="margin-left:25px" class="iconfont">&#xe641;</i>
                请假预警，超警戒值{{warn.work.value}}%
              </p>
              <matts
                :data="workData"
                v-loading="attendLoading"
                element-loading-background="rgba(0, 0, 0, 0)"
              ></matts>
            </div>
          </li>
          <li class="warn-box">
            <div class="title-box">
              <h3 class="title">
                <em></em>
                今日晨检统计
                <i class="percent">疾病预警值{{warnPercent.temp}}%</i>
              </h3>
              <el-message :value="warnPercent.temp" :item="'体温'" @getMessage="getMessage(arguments, 'temp')"></el-message>
            </div>
            <div class="content">
              <p class="warn" v-if="warn.temp.show">
                <i style="margin-left:17px" class="iconfont">&#xe641;</i>
                疾病预警，超警戒值{{warn.temp.value}}%
              </p>
              <ul
                class="echarts-box"
                v-loading="attendLoading"
                element-loading-background="rgba(0, 0, 0, 0)"
                v-if="tempSettings.data.findIndex(v => v !== 0) !== -1"
              >
                <li>
                  <bar
                    :settings="tempSettings"
                  ></bar>
                </li>
                <li>
                  <bar
                    :settings="snacksSettings"
                  ></bar>
                </li>
              </ul>
              <no-data v-else :item="'晨检统计'" />
            </div>
          </li>
          <li>
            <h3 class="title">
              <em></em>幼儿园信息</h3>
            <div class="content">
              <matts :data="KindergartenData"></matts>
            </div>
          </li>
          <li>
            <h3 class="title">
              <em></em>教师信息</h3>
            <ul class="content">
              <li>
                <pie
                  :data="jobData.data"
                  :color="jobData.color"
                  :title="jobData.title"
                  :pieSize="jobData.pieSize"
                  v-if="jobData.data[0].name !== '暂无数据'"
                ></pie>
                <no-data v-else :item="'职称'" />
              </li>
              <li>
                <pie
                  :data="ageData.data"
                  :color="ageData.color"
                  :title="ageData.title"
                  :pieSize="ageData.pieSize"
                  v-if="ageData.data[0].name !== '暂无数据'"
                ></pie>
                <no-data v-else :item="'年龄'" />
              </li>
            </ul>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import ElMap from '@/common/echarts/Map'
import Live from '@/common/ezviz/Live'
import Bar from '@/common/echarts/Bar'
import Matts from './components/Matts'
import Pie from '@/common/echarts/Pie'
import http from '@/common/axios/http'
import api from '@/common/axios/api'
import ElMessage from '@/common/elementUi/ElMessage'
export default {
  name: 'HealthyToday',
  components: {
    ElMap,
    Bar,
    Matts,
    Pie,
    Live,
    ElMessage
  },
  data () {
    return {
      attendLoading: true,
      loading: true,
      warnPercent: {
        work: '30',
        temp: '30'
      },
      warn: {
        work: {
          show: false,
          value: 0
        },
        temp: {
          show: false,
          value: 0
        }
      },
      // 地图标签数据
      provinceId: localStorage.getItem('provinceId'),
      cityId: localStorage.getItem('cityId'),
      districtId: localStorage.getItem('districtId'),
      gardenName: localStorage.getItem('gardenName'),
      gardenId: localStorage.getItem('gardenId'),
      tagInfoData: [],
      gardenRecord: {
        name: '拱墅区植材小学',
        location: '拱墅区'
      },
      // 以出勤记录情况排名
      attendance: {
        xData: ['暂无数据'],
        maxVal: 10,
        data: ['0'],
        linearGradient: ['#53e9ee', '#325fff'],
        hasLine: true,
        labelHide: true,
        labelNuit: '{c}%',
        yName: '百分比(%)',
        grid: {
          top: '20%',
          left: '0%',
          right: '0%',
          bottom: '0%',
          containLabel: true
        }
      },
      // 以体温报警人数排名
      temperature: {
        xData: ['暂无数据'],
        data: ['0'],
        maxVal: 20,
        linearGradient: ['#ff9786', '#ffe98f'],
        labelHide: true,
        hasLine: true,
        yName: '人数（人）',
        grid: {
          top: '20%',
          left: '0%',
          right: '0%',
          bottom: '0%',
          containLabel: true
        }
      },
      // 今日晨检统计
      tempSettings: {
        xData: ['体温正常', '体温偏高'],
        data: [0, 0],
        linearGradient: ['#ffe98f', '#2dffff'],
        maxVal: 70,
        yHide: true,
        labelHide: true,
        grid: {
          top: '5%',
          left: '0%',
          right: '0%',
          bottom: '0%',
          containLabel: true
        }
      },
      snacksSettings: {
        xData: ['需要加餐', '需要服药', '需要观察'],
        data: [0, 0, 0],
        linearGradient: ['#2de7ff', '#ff6dff'],
        maxVal: 70,
        yHide: true,
        labelHide: true,
        grid: {
          top: '5%',
          left: '0%',
          right: '0%',
          bottom: '0%',
          containLabel: true
        }
      },
      // 今日出勤统计
      workData: [
        {
          unit: '人',
          subText: '已出勤人数',
          sum: 0
        },
        {
          unit: '人',
          subText: '请假人数',
          sum: 0
        },
        {
          unit: '人',
          subText: '预估缺勤人数',
          sum: 0
        }
      ],
      // 幼儿园信息
      KindergartenData: [
        {
          unit: '家',
          subText: '园所总数',
          sum: 0
        },
        {
          unit: '人',
          subText: '园长总数',
          sum: 0
        },
        {
          unit: '人',
          subText: '在职教师总数',
          sum: 0
        },
        {
          unit: '人',
          subText: '在园儿童总数',
          sum: 0
        }
      ],
      // 职称分布
      jobData: {
        data: [
          { value: 0, name: '正高级' },
          { value: 0, name: '高级' },
          { value: 0, name: '一级' },
          { value: 0, name: '二级' },
          { value: 0, name: '三级' }
        ],
        color: ['#ffbc00', '#57d0ff', '#4d6bff', '#ff6562', '#9d70ff'],
        title: '职称分布',
        pieSize: {
          radius: ['35%', '45%'],
          center: ['50%', '35%'],
          title: {
            top: 52,
            fontSize: 12
          }
        }
      },
      // 年龄分布
      ageData: {
        data: [
          { value: 0, name: '00后' },
          { value: 0, name: '90后' },
          { value: 0, name: '80后' },
          { value: 0, name: '70后' }
        ],
        color: ['#1683fb', '#61ffe1', '#24cbff', '#7189ff'],
        title: '年龄分布',
        pieSize: {
          radius: ['35%', '45%'],
          center: ['50%', '35%'],
          title: {
            top: 52,
            fontSize: 12
          }
        }
      }
    }
  },
  mounted () {
    if (localStorage.getItem('workVal')) { this.warnPercent.work = localStorage.getItem('workVal') }
    if (localStorage.getItem('tempVal')) { this.warnPercent.temp = localStorage.getItem('tempVal') }
    // 初始化所有边框样式
    this.$common.border(window.$('.bottom .content'))
    // 5分钟刷新一次
    setInterval(() => {
      this.refresh()
    }, 300000)
    this.getAttendCount()
    this.getGardenInfos()
    this.getAttendRage()
  },
  methods: {
    // 修改报警
    getMessage (data, name) {
      switch (name) {
        case 'work':
          this.warnPercent.work = data[0]
          localStorage.setItem('workVal', data[0])
          break
        case 'temp':
          this.warnPercent.temp = data[0]
          localStorage.setItem('tempVal', data[0])
          break
      }
      this.refresh()
    },
    // 刷新
    refresh () {
      window.$('.refresh').find('i').addClass('loading')
      this.getAttendCount()
      this.getGardenInfos()
      this.getAttendRage()
    },
    // 出勤的统计
    getAttendCount: async function () {
      const res = await http.get(api.attendGarden, {
        gardenId: this.gardenId
      })
      let d = res.data.data
      const work = d.leaveCount / d.totalCount * 100 >= parseFloat(this.warnPercent.work)
      if (work) {
        this.warn.work = {
          show: true,
          value: (d.leaveCount / d.totalCount * 100 - parseFloat(this.warnPercent.work)).toFixed(2)
        }
      } else {
        this.warn.work = {
          show: false,
          value: (d.leaveCount / d.totalCount * 100 - parseFloat(this.warnPercent.work)).toFixed(2)
        }
      }
      const temp = (d.gcCount + d.fyCount) / d.totalCount * 100 >= parseFloat(this.warnPercent.temp)
      if (temp) {
        this.warn.temp = {
          show: true,
          value: ((d.gcCount + d.fyCount) / d.totalCount * 100 - parseFloat(this.warnPercent.temp)).toFixed(2)
        }
      } else {
        this.warn.temp = {
          show: false,
          value: ((d.gcCount + d.fyCount) / d.totalCount * 100 - parseFloat(this.warnPercent.temp)).toFixed(2)
        }
      }
      // 今日出勤统计
      this.workData[0].sum = d.attendCount
      this.workData[1].sum = d.leaveCount
      this.workData[2].sum = d.totalCount - d.attendCount
      // 今日晨检统计
      this.tempSettings.data = [d.normalTempCount, d.warnTempCount]
      this.snacksSettings.data = [d.jcCount, d.fyCount, d.gcCount]
    },
    // 幼儿园信息
    getGardenInfos: async function () {
      const res = await http.get(api.getGardenInfos, {
        gardenId: this.gardenId
      })
      let d = res.data.data
      console.log(d, '123123123123123123123')
      this.KindergartenData[0].sum = d.gardenCount
      this.KindergartenData[1].sum = d.kindergartenCount
      this.KindergartenData[2].sum = d.teacherCount
      this.KindergartenData[3].sum = d.totalCount
      const ages = ['00', '90', '80', '70']
      const jobs = ['正高级', '高级', '一级', '二级', '三级']
      this.jobData.data = [
        { value: 0, name: '正高级' },
        { value: 0, name: '高级' },
        { value: 0, name: '一级' },
        { value: 0, name: '二级' },
        { value: 0, name: '三级' }
      ]
      this.ageData.data = [
        { value: 0, name: '00后' },
        { value: 0, name: '90后' },
        { value: 0, name: '80后' },
        { value: 0, name: '70后' }
      ]
      if (d.jobs.length) {
        for (let i = 0, len = this.jobData.data.length; i < len; i++) {
          const result = d.jobs.filter((item) => {
            return item.item1 === jobs[i]
          })
          result.length ? this.jobData.data[i].value = result[0].item2 : this.jobData.data[i].value = 0
        }
      } else {
        this.jobData.data = [{ name: '暂无数据', value: 0 }]
      }
      if (d.ages.length) {
        for (let i = 0, len = this.ageData.data.length; i < len; i++) {
          const result = d.ages.filter((item) => {
            // return item.item1 === ages[i]
            return item.item1.substring(0, 1) === ages[i].substring(0, 1)
          })
          result.length ? this.ageData.data[i].value = result.length : this.ageData.data[i].value = 0
        }
      } else {
        this.ageData.data = [{ name: '暂无数据', value: 0 }]
      }
    },
    // 班级的排名
    getAttendRage: async function () {
      const result = await http.get(api.getAttendRages, {
        gardenId: this.gardenId
      })
      const res = result.data.data
      if (res.length) {
        this.attendLoading = false
        window.$('.refresh').find('i').removeClass('loading')
        const defaultObj = {
          totalCount: 0,
          attendCount: 0,
          warnCount: 0,
          jcCount: 0,
          fyCount: 0,
          gcCount: 0
        }
        for (let i = 0; i < res.length; i++) {
          res[i] = window.$.extend({}, defaultObj, res[i])
        }

        for (let i = 0, len = res.length; i < len; i++) {
          if (res[i].gradeNum === 1) {
            res[i].name = '托班'
          } else if (res[i].gradeNum === 2) {
            res[i].name = '小班'
          } else if (res[i].gradeNum === 3) {
            res[i].name = '中班'
          } else if (res[i].gradeNum === 4) {
            res[i].name = '大班'
          }
        }
        for (let i = 0, len = res.length; i < len; i++) {
          res[i].rate = (((res[i].attendCount) / (res[i].totalCount) * 100).toFixed(2))
        }
        const result = res.filter((item) => {
          return (item['name'] && (item.attendCount !== 0))
        })
        if (result.length) {
          let rate = {
            attend: result,
            temperature: result
          }
          rate.attend.sort(function (a, b) {
            return b.rate - a.rate
          })
          let attendCountName = []
          let attendCountRate = []
          for (let i = 0, len = rate.attend.length; i < len; i++) {
            attendCountName.push(rate.attend[i].name)
            attendCountRate.push(rate.attend[i].rate)
          }
          this.attendance.xData = attendCountName
          this.attendance.data = attendCountRate
          this.attendance.maxVal = Math.max(...attendCountRate)
          console.log(attendCountName, attendCountRate)
          rate.temperature.sort(function (a, b) {
            return b.warnCount - a.warnCount
          })
          let warnDataName = []
          let warnDataCount = []
          for (let i = 0; i < rate.temperature.length; i++) {
            warnDataName.push(rate.temperature[i].name)
            warnDataCount.push(rate.temperature[i].warnCount)
          }
          this.temperature.xData = warnDataName
          this.temperature.data = warnDataCount
          this.temperature.maxVal = Math.max(...warnDataCount)
        } else {
          this.temperature.xData = ['暂无数据']
          this.temperature.data = ['0']
          this.attendance.data = ['0']
          this.attendance.xData = ['暂无数据']
        }
      } else {
        this.temperature.xData = ['暂无数据']
        this.temperature.data = ['0']
        this.attendance.data = ['0']
        this.attendance.xData = ['暂无数据']
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
.handle-no-data
  display flex
  align-items center
  justify-content center
  .one
    margin-top 50px
</style>



